.createNewFeedback {
  h1 {
    margin-bottom: 40px;
  }

  input[type="submit"] {
    background-color: $color-purple;
    color: #fff;
  }
}

@media screen and (max-width: 400px) {
  .createNewFeedback {
    form {
      input[type="submit"] {
        width: 100%;
      }
    }
  }
}
